<template>
  <div>
    <van-nav-bar
	title="注册"
	left-text="登录"
	left-arrow 
	@click-left="onClickLeft"
	/>
    <!-- 网站logo -->
    <div class="logo">
      <img src="../assets/images/logo.png" />
    </div>
    <!-- form表单 -->
    <van-form @submit="register">
      <van-field type="text" label="手机号" placeholder="手机号" v-model="userInfo.phone">
        <template v-slot:left-icon>
          <i class="fa fa-phone fa-lg" aria-hidden="true"></i>
        </template>
      </van-field>

      <van-field type="text" label="昵称" placeholder="昵称" v-model="userInfo.nickName">
        <template v-slot:left-icon>
          <i class="fa fa-user fa-lg" aria-hidden="true"></i>
        </template>
      </van-field>
      <!-- 密码 -->
      <van-field
        :type="ishas?'password':'text'"
        label="密码"
        placeholder="密码"
        v-model="userInfo.password"
        @click-right-icon="viewPassword"
        @input="passwordIshas"
      >
        <template v-slot:left-icon>
          <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
        </template>
        <template v-slot:right-icon>
          <i class="fa fa-lg" :class="ishas?'fa-eye-slash':'fa-eye'" aria-hidden="true"></i>
          <!-- fa-eye-slash -->
        </template>
      </van-field>
      <!-- 确认密码 -->
      <van-field
        :type="ishas?'password':'text'"
        label="再次输入密码"
        placeholder="输入密码"
        v-model="password_demo"
        :disabled="password_demo_disabled"
        @click-right-icon="viewPassword"
      >
        <template v-slot:left-icon>
          <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
        </template>
      </van-field>
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          <!-- " -->
          立即注册
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

  export default {
    data() {
      return {

        //用户注册信息
        userInfo: {
          phone: '',
          nickName: '',
          password: ''
        },
		password_demo:'',
        //false: 闭合，true：睁开
        ishas: true,
		password_demo_disabled:true,
		img:require('../assets/images/right.png')
      }
    },

    methods: {

      //切换密码的格式
      viewPassword() {
        this.ishas = !this.ishas;
      },

      //注册
      register() {

        //进行验证表单
        if (validForm.validUserForm(this.userInfo, this)) {

          //复制对象
          let data = Object.assign({}, this.userInfo);

          //请求密钥
          data.appkey = this.appkey;

          //开启加载提示
          this.$toast.loading({
            message: '加载中...',
            forbidClick: true,

            //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
            duration:0
          });
			//判断两次密码是否一致
			if(this.userInfo.password!=this.password_demo){
				this.$toast('两次密码不一致')
			}else{
				//发起注册请求
         		this.axios({
         		  method: 'POST',
         		  url: '/register',

         		  //post传递参数需要写在data
         		  data
         		}).then(result => {
         		  //关闭加载提示
         		  this.$toast.clear();
				   
				   if(result.data.code==100){
					   let that = this
					   this.$toast({
						   message: '注册成功',
							  icon: this.img,
							  onClose:function(){
								//进入登录页面
					   			that.$router.push({name:'login'})
							  }
					   })
					   
				   }else{
             this.$toast.fail(result.data.msg)
           }
         		}).catch(() => {
         		  //关闭加载提示
         		  this.$toast.clear();
         		  
         		})
			}
          
        }

    
  },
  //判断密码输入正确时打开输入框
    passwordIshas() {
      let reg = validForm.validUserFormReg.password.reg;

      if (reg.test(this.userInfo.password)) {
        this.password_demo_disabled = false;
      }
	},
	
	//点击导航栏左侧按钮进入登录页面
	onClickLeft(){
		this.$router.push({name:'login'})
		
	}
  
}}
</script>

<style lang="less" scoped>
@c: #ff8800;
.logo {
  width: 100px;
  height: 100px;
  margin: 60px auto;
}
//更改register导航颜色
/deep/ .van-nav-bar__text {
  color: @c;
}
/deep/ .van-icon-arrow-left {
  color: @c;
}
//更改提交按钮颜色
.van-button {
  background-color: @c;
  border: none;
}
</style>
